<template>
  <div class="page-content">

      <div class="page-datatable">




            <Table :columns="table.columns" :data="table.data.data" @on-sort-change="changeSort" size="small"></Table>



          <div class="pagination-wrapper">
            <Page :total="table.data.total" @on-change="getData" size="small" :page-size="table.data.per_page"></Page>
          </div>



      </div>

      <div class="page-aside">

        <div class="side-header">
          <Button type="success" icon="ivu-icon ivu-icon-plus" @click="table.showForm = true">Агуулах нэмэх</Button>
          <Button type="ghost" icon="ivu-icon ivu-icon-ios-settings-strong"  @click="$parent.$options.methods.showSettingsModal($parent)">Тохиргоо</Button>
        </div>


        <div>
          <h2>
            Мэдээлэл шүүх
          </h2>


        </div>

        <Form >

          <FormItem label="Төрөл">
            <Select v-model="filters.type" placeholder="Төрөл" clearable  >
              <Option :value="type_.id" v-for="type_ in $parent.type.data" :key="type_.id">{{type_.wr_type_name}}</Option>
            </Select>
          </FormItem>


          <FormItem label="Түлхүүр үг">
            <Input type="text" v-model="filters.text" placeholder="Түлхүүр үг" />

          </FormItem>


          <Button type="primary" @click="doFilter">Шүүх</Button>
          <Button type="ghost"  @click="resetFileter">Цэвэрлэх</Button>

        </Form>
      </div>



    <Modal
      v-model="table.showForm"
      title="Агуулах"
    >



      <Form ref="table.form" :model="table.form" :rules="table.rule">
        <FormItem prop="name">
          <FormItem label="Төрөл">
            <Select v-model="table.form.warehouse_type_id" placeholder="Төрөл">
              <Option :value="type_.id" v-for="type_ in $parent.type.data" :key="type_.id">{{type_.wr_type_name}}</Option>
            </Select>
          </FormItem>

        </FormItem>
        <FormItem prop="warehouse" label="Нэр">
          <Input type="text" v-model="table.form.warehouse" placeholder="Нэр" />

        </FormItem>


      </Form>

      <div slot="footer">
        <Button type="ghost" @click="resetForm('table.form')">Болих</Button>
        <Button type="primary" @click="handleSubmit('table.form')">Хадгалах</Button>
      </div>
    </Modal>

  </div>

</template>

<script>
  import {warehouseList, warehouseAdd, warehouseUpdate, warehouseDelete} from '../../resoures/index'
  export default {
    methods:{
      resetFileter(){
        this.filters = {
          type:undefined,
          text:undefined
        };
        this.doFilter();
      },
      doFilter(){
        this.table.options.type = this.filters.type;
        this.table.options.searchWord = this.filters.text;

        this.getData(1);
    },
      changeSort(order){
        if(order.key == 'warehouse'){
          this.table.columns[1].sortType = order.order

        }
        if(order.key == 'wr_type_name'){
          this.table.columns[0].sortType = order.order
        }

        this.table.options.orderColumn = order.key;
        this.table.options.orderTo = order.order;

        this.getData(1);

        return false;
      },
      getData(page){
        warehouseList(this.table.options, page ? page : 1).then(response=>{
          this.table.data = response.data
        }).catch(error=>{
          this.$Message.error('Алдаа');
        })
      },

      resetForm(name){
        if(name == 'table.form'){
          this.table.form = {
            warehouse: '',
            warehouse_type_id: null,
          };
          this.table.editing = null;
          this.table.showForm = false;
        }
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            if(name == 'table.form'){
              if(this.table.editing){
                warehouseUpdate({id:this.table.editing, warehouse:this.table.form.warehouse, warehouse_type_id:this.table.form.warehouse_type_id}).then(response=>{
                  if(response.data == 'success'){
                    this.table.form = {
                      warehouse: '',
                      warehouse_type_id: null,
                    };
                    this.table.editing = null;
                    this.$Message.success('Хадаглагдлаа!');
                    this.table.showForm = false;
                    this.getData(1);
                  }

                }).catch(error=>{
                  this.$Message.error('Алдаа!');
                })
              } else {
                warehouseAdd({warehouse:this.table.form.warehouse, warehouse_type_id:this.table.form.warehouse_type_id}).then(response=>{
                  if(response.data == 'success'){
                    this.table.form = {
                      warehouse: '',
                      warehouse_type_id: null,
                    };
                    this.$Message.success('Хадаглагдлаа!');
                    this.table.showForm = false;
                    this.getData(1);
                  }

                }).catch(error=>{
                  this.$Message.error('Алдаа!');
                })
              }

            }

          } else {
            this.$Message.error('Мэдээллээ бүрэн оруулна уу!');
          }
        })
      },
      deteteType(id){

        warehouseDelete({id:id}).then(response=>{
          this.getData(1);
        })
      },
      updateType(id, warehouse, warehouse_type_id){
        this.table.editing = id;
        this.table.showForm = true;
        this.table.form.warehouse = warehouse;
        this.table.form.warehouse_type_id = warehouse_type_id;
      }
    },
    data() {
      return {


        table:{
          options:{
            type:undefined,
            orderColumn:undefined,
            orderTo:undefined,
            searchWord:undefined,
          },
          showForm:false,
          editing:null,
          form: {
            warehouse: '',
            warehouse_type_id: null,
          },
          rule: {
            warehouse: [
              { required: true, message: 'Агуулахын нэр оруулна уу', trigger: 'blur' },
            ],
            warehouse_type_id: [
              { required: true, message: 'Агуулахын төрөл сонгоно уу', trigger: 'blur' },
            ],
          },
          columns: [
            {
              title: 'Агуулахын төрөл',
              key: 'wr_type_name',
              sortable: true
            },
            {
              title: 'Агуулах',
              key: 'warehouse',
              sortable: true
            },
            {
              title: '...',
              align: 'center',
              key: 'id',
              width: 150,
              render: (h, params) => {
                return h('div', [
                  h('Button', {
                    props: {
                      type: 'primary',
                      size: 'small',

                    },
                    style: {
                      marginRight: '5px'
                    },
                    on: {
                      click: () => {

                        this.updateType(params.row.id, params.row.warehouse, params.row.warehouse_type_id)
                      }
                    }
                  }, 'Засах'),
                  h('Poptip', {
                    props: {
                      confirm: true,
                      title: 'Устгахдаа итгэлтэй байна уу !',
                      okText:'Устгах',
                      cancelText:'Болих',

                      placement:"left-start"

                    },
                    on: {
                      "on-ok": () => {

                        this.deteteType(params.row.id)
                      }
                    }

                  }, [
                    h('Button', {
                      props: {
                        type: 'error',
                        size: 'small'
                      },

                    }, 'Устгах'),
                  ]),



                ]);
              }
            },

          ],
          data:{
            last_page:1,
            total:0,
            per_page:10,
            data:[]
          }
        },

        filters:{
          type:undefined,
          text:undefined
        }


      }
    },
    mounted(){
      this.getData(1);

    }
  }
</script>



