import Vue from 'vue'
import Router from 'vue-router'
import warehouse from '@/components/warehouse/index'
import WareHouses from '@/components/warehouse/WareHouses'
import Products from '@/components/warehouse/Products'
import WhareHouseTransaction from '@/components/warehouse/Transaction'
import Transaction from '@/components/Transaction'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/warehouse',
      component: warehouse,
      children: [
        {
          path: '/',
          component: WareHouses
        },
        {
          path: 'product',
          component: Products
        },
        {
          path: 'transaction',
          component: WhareHouseTransaction
        }
      ]
    },
    {
      path: '/transaction',
      component: Transaction
    }

  ]
})



