<template>
  <section class="page page-driver">
   <div class="sub-header page-header">
     <ul class="sub-menu">
       <li>
         <router-link to="/warehouse/" exact>Агуулахын жагсаалт</router-link>
       </li>
       <li>
         <router-link to="/warehouse/product">Бараа</router-link>
       </li>
       <li>
         <router-link to="/warehouse/transaction">Агуулах хоорондын шилжилт</router-link>
       </li>
     </ul>
   </div>

    <router-view/>

    <Modal
      v-model="showSettings"
      title="Агуулахын тохиргоо"
    >

      <Table :columns="type.columns" :data="type.data" v-if="!type.showForm"></Table>

      <Form ref="type.form" :model="type.form" :rules="type.rule" v-if="type.showForm">
        <FormItem prop="name">
          <Input type="text" v-model="type.form.name" placeholder="Төрөл" />

        </FormItem>

        <FormItem>
          <Button type="ghost" @click="resetForm('type.form')">Болих</Button>
          <Button type="primary" @click="handleSubmit('type.form')">Хадгалах</Button>
        </FormItem>
      </Form>

      <div slot="footer">
        <Button type="success" icon="ivu-icon ivu-icon-plus" size="small" @click="type.showForm = true">Агуулахын төрөл нэмэх</Button> <Button type="ghost" size="large"  @click="showSettings = false">Хаах</Button>
      </div>
    </Modal>

    <Modal
      v-model="showSettings2"
      title="Барааны тохиргоо"
    >

      <Tabs value="name1" :animated="false">
        <TabPane label="Барааны төрөл" name="name1">

          <Table :columns="productType.columns" :data="productType.data" v-if="!productType.showForm"></Table>

          <Form ref="productType.form" :model="productType.form" :rules="productType.rule" v-if="productType.showForm">
            <FormItem >
              <Select v-model="productType.form.parent" placeholder="Төрөл" clearable>
                <Option :value="type_.id" v-for="type_ in productType.data" :key="type_.id">{{type_.pro_type_name}}</Option>
              </Select>
            </FormItem>
            <FormItem prop="name">
              <Input type="text" v-model="productType.form.name" placeholder="Төрөл" />

            </FormItem>

            <FormItem>
              <Button type="ghost" @click="resetForm('productType.form')">Болих</Button>
              <Button type="primary" @click="handleSubmit('productType.form')">Хадгалах</Button>
            </FormItem>
          </Form>
          <Button type="success" icon="ivu-icon ivu-icon-plus" size="small" @click="productType.showForm = true">Барааны төрөл нэмэх</Button> <Button type="ghost" size="large"  @click="showSettings2 = false">Хаах</Button>



        </TabPane>
        <TabPane label="Барааны багц" name="name2">


          <Table :columns="productPack.columns" :data="productPack.data" v-if="!productPack.showForm"></Table>

          <Form ref="productPack.form" :model="productPack.form" :rules="productPack.rule" v-if="productPack.showForm">
            <FormItem prop="name">
              <Input type="text" v-model="productPack.form.name" placeholder="Багц" />

            </FormItem>

            <FormItem>
              <Button type="ghost" @click="resetForm('productPack.form')">Болих</Button>
              <Button type="primary" @click="handleSubmit('productPack.form')">Хадгалах</Button>
            </FormItem>
          </Form>
          <Button type="success" icon="ivu-icon ivu-icon-plus" size="small" @click="productPack.showForm = true">Барааны багц нэмэх</Button> <Button type="ghost" size="large"  @click="showSettings2 = false">Хаах</Button>


        </TabPane>

      </Tabs>


      <div slot="footer">

      </div>
    </Modal>




  </section>

</template>

<script>
  import {

    warehouseTypeList, warehouseTypeAdd, warehouseTypeUpdate, warehouseTypeDelete,

    productTypeList, productTypeAdd, productPackUpdate, productTypeDelete,

    productPackList, productPackAdd, productTypeUpdate, productPackDelete

  } from '../../resoures/index'
  export default {
    methods:{
      getTypes(){
        warehouseTypeList().then(response=>{
          this.type.data = response.data
        }).catch(error=>{
          this.$Message.error('Алдаа');
        })
      },
      getPTypes(){
        productTypeList().then(response=>{
          this.productType.data = response.data
        }).catch(error=>{
          this.$Message.error('Алдаа');
        })
      },
      getPP(){
        productPackList().then(response=>{
          this.productPack.data = response.data
        }).catch(error=>{
          this.$Message.error('Алдаа');
        })
      },
      showSettingsModal(this_){
        this_.showSettings =true;
      },
      showSettingsModal2(this_){
        this_.showSettings2 =true;
      },
      resetForm(name){
        if(name == 'type.form'){
          this.type.form = {
            name: '',
          };
          this.type.editing = null;
          this.type.showForm = false;
        }
        if(name == 'productType.form'){
          this.productType.form = {
            name: '',
            parent: null,
          };
          this.productType.editing = null;
          this.productType.showForm = false;
        }
        if(name == 'productPack.form'){
          this.productPack.form = {
            name: '',
          };
          this.productPack.editing = null;
          this.productPack.showForm = false;
        }
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            if(name == 'type.form'){
              if(this.type.editing){
                warehouseTypeUpdate({id:this.type.editing, name:this.type.form.name}).then(response=>{
                  if(response.data == 'success'){
                    this.type.form = {
                      name: '',
                    };
                    this.type.editing = null;
                    this.$Message.success('Хадаглагдлаа!');
                    this.type.showForm = false;
                    this.getTypes();
                  }

                }).catch(error=>{
                  this.$Message.error('Алдаа!');
                })
              } else {
                warehouseTypeAdd({name:this.type.form.name}).then(response=>{
                  if(response.data == 'success'){
                    this.type.form = {
                      name: '',
                    };
                    this.$Message.success('Хадаглагдлаа!');
                    this.type.showForm = false;
                    this.getTypes();
                  }

                }).catch(error=>{
                  this.$Message.error('Алдаа!');
                })
              }

            }

            if(name == 'productType.form'){
              if(this.productType.editing){
                productTypeUpdate({id:this.productType.editing, name:this.productType.form.name, parent:this.productType.form.parent}).then(response=>{
                  if(response.data == 'success'){
                    this.productType.form = {
                      name: '',
                      parent: null,
                    };
                    this.productType.editing = null;
                    this.$Message.success('Хадаглагдлаа!');
                    this.productType.showForm = false;
                    this.getPTypes();
                  }

                }).catch(error=>{
                  this.$Message.error('Алдаа!');
                })
              } else {
                productTypeAdd({name:this.productType.form.name, parent:this.productType.form.parent}).then(response=>{
                  if(response.data == 'success'){
                    this.productType.form = {
                      name: '',
                      parent: null,
                    };
                    this.$Message.success('Хадаглагдлаа!');
                    this.productType.showForm = false;
                    this.getPTypes();
                  }

                }).catch(error=>{
                  this.$Message.error('Алдаа!');
                })
              }

            }

            if(name == 'productPack.form'){
              if(this.productPack.editing){
                productPackUpdate({id:this.productPack.editing, name:this.productPack.form.name}).then(response=>{
                  if(response.data == 'success'){
                    this.productPack.form = {
                      name: '',
                    };
                    this.productPack.editing = null;
                    this.$Message.success('Хадаглагдлаа!');
                    this.productPack.showForm = false;
                    this.getPP();
                  }

                }).catch(error=>{
                  this.$Message.error('Алдаа!');
                })
              } else {
                productPackAdd({name:this.productPack.form.name}).then(response=>{
                  if(response.data == 'success'){
                    this.productPack.form = {
                      name: '',
                    };
                    this.$Message.success('Хадаглагдлаа!');
                    this.productPack.showForm = false;
                    this.getPP();
                  }

                }).catch(error=>{
                  this.$Message.error('Алдаа!');
                })
              }

            }

          } else {
            this.$Message.error('Мэдээллээ бүрэн оруулна уу!');
          }
        })
      },
      deteteType(id){

        warehouseTypeDelete({id:id}).then(response=>{
          this.getTypes();
        })
      },
      updateType(id, name){
        this.type.editing = id;
        this.type.showForm = true;
        this.type.form.name = name;
      },
      detetePType(id){

        productTypeDelete({id:id}).then(response=>{
          this.getPTypes();
        })
      },
      updatePType(id, name, parent){
        this.productType.editing = id;
        this.productType.showForm = true;
        this.productType.form.name = name;
        this.productType.form.parent = parent;
      },
      detetePP(id){

        productPackDelete({id:id}).then(response=>{
          this.getPP();
        })
      },
      updatePP(id, name){
        this.productPack.editing = id;
        this.productPack.showForm = true;
        this.productPack.form.name = name;
      }
    },
    data() {
      return {

        showSettings:false,
        showSettings2:false,
        type:{
          showForm:false,
          editing:null,
          form: {
            name: '',
          },
          rule: {
            name: [
              { required: true, message: 'Агуулахын төрөл оруулна уу', trigger: 'blur' }
            ],
          },
          columns: [
            {
              title: ' Агуулахын төрөл',
              key: 'wr_type_name'
            },
            {
              title: 'Үйлдэл',
              key: 'id',
              width:100,
              render: (h, params) => {
                return h('div', [
                  h('Button', {
                    props: {
                      type: 'ghost',
                      size: 'small',
                      icon: 'ivu-icon ivu-icon-edit',
                    },
                    on: {
                      click: () => {

                        this.updateType(params.row.id, params.row.wr_type_name)
                      }
                    }
                  }),
                  h('Poptip', {
                    props: {
                      confirm: true,
                      title: 'Устгахдаа итгэлтэй байна уу !',
                      okText:'Устгах',
                      cancelText:'Болих',
                      placement:"left-start"

                    },
                    on: {
                      "on-ok": () => {

                        this.deteteType(params.row.id)
                      }
                    }

                  }, [
                    h('Button', {
                      props: {
                        type: 'ghost',
                        size: 'small',
                        icon: 'ivu-icon ivu-icon-trash-a',
                      },

                    }),
                  ]),



                ]);
              }
            },

          ],
          data:[]
        },
        productType:{
          showForm:false,
          editing:null,
          form: {
            name: '',
            parent: null,
          },
          rule: {
            name: [
              { required: true, message: 'Барааны төрөл оруулна уу', trigger: 'blur' }
            ],
          },
          columns: [
            {
              title: ' Барааны төрөл',
              key: 'pro_type_name'
            },
            {
              title: 'Үйлдэл',
              key: 'id',
              width:100,
              render: (h, params) => {
                return h('div', [
                  h('Button', {
                    props: {
                      type: 'ghost',
                      size: 'small',
                      icon: 'ivu-icon ivu-icon-edit',
                    },
                    on: {
                      click: () => {

                        this.updatePType(params.row.id, params.row.pro_type_name, params.row.parent_id)
                      }
                    }
                  }),
                  h('Poptip', {
                    props: {
                      confirm: true,
                      title: 'Устгахдаа итгэлтэй байна уу !',
                      okText:'Устгах',
                      cancelText:'Болих',
                      placement:"left-start"

                    },
                    on: {
                      "on-ok": () => {

                        this.detetePType(params.row.id)
                      }
                    }

                  }, [
                    h('Button', {
                      props: {
                        type: 'ghost',
                        size: 'small',
                        icon: 'ivu-icon ivu-icon-trash-a',
                      },

                    }),
                  ]),



                ]);
              }
            },

          ],
          data:[]
        },
        productPack:{
          showForm:false,
          editing:null,
          form: {
            name: '',
          },
          rule: {
            name: [
              { required: true, message: 'Багцын нэр оруулна уу', trigger: 'blur' }
            ],
          },
          columns: [
            {
              title: ' Багц',
              key: 'pack_name'
            },
            {
              title: 'Үйлдэл',
              key: 'id',
              width:100,
              render: (h, params) => {
                return h('div', [
                  h('Button', {
                    props: {
                      type: 'ghost',
                      size: 'small',
                      icon: 'ivu-icon ivu-icon-edit',
                    },
                    on: {
                      click: () => {

                        this.updatePP(params.row.id, params.row.pack_name)
                      }
                    }
                  }),
                  h('Poptip', {
                    props: {
                      confirm: true,
                      title: 'Устгахдаа итгэлтэй байна уу !',
                      okText:'Устгах',
                      cancelText:'Болих',
                      placement:"left-start"

                    },
                    on: {
                      "on-ok": () => {

                        this.detetePP(params.row.id)
                      }
                    }

                  }, [
                    h('Button', {
                      props: {
                        type: 'ghost',
                        size: 'small',
                        icon: 'ivu-icon ivu-icon-trash-a',
                      },

                    }),
                  ]),



                ]);
              }
            },

          ],
          data:[]
        },



      }
    },
    mounted(){
      this.getTypes();
      this.getPTypes();
      this.getPP();
    }
  }
</script>



