<template>
  <Layout>
    <Content>


      <div style="position: relative">
        <Table :columns="table.columns" :data="table.data.data" @on-sort-change="changeSort"></Table>
      </div>
      <br>
      <Page :total="table.data.total" @on-change="getData" :page-size="table.data.per_page"></Page>

    </Content>
    <Sider hide-trigger width="300">

      <div class="">
        <Button type="success" icon="ivu-icon ivu-icon-plus" size="small" @click="table.showForm = true">Нийлүүлэлт хийх</Button>

      </div>
      <br>

      <div>
        <h4>
          Хайлт
        </h4>





      </div>

      <Form >
        <FormItem   label="Төрөл">

          <Select v-model="filters.transaction_type" placeholder="Төрөл" clearable>
            <Option :value="`${type_.id}`" v-for="type_ in transaction_types" :key="type_.id">{{type_.trans_type}}</Option>
          </Select>



        </FormItem>
        <FormItem   label="Бараа">

          <Select v-model="filters.product" placeholder="Бараа" clearable>
            <Option :value="`${type_.id}`" v-for="type_ in products" :key="type_.id">{{type_.product_name}}</Option>
          </Select>



        </FormItem>

        <FormItem label="Агуулах">


          <Select v-model="filters.warehouse" placeholder="Агуулах" clearable>
            <Option :value="`${warehouse.id}`" v-for="warehouse in warehouses" :key="warehouse.id">{{warehouse.warehouse}}</Option>
          </Select>


        </FormItem>

        <FormItem label="Харилцагч">


          <Select v-model="filters.customer" placeholder="Харилцагч" clearable>
            <Option :value="`${warehouse.id}`" v-for="warehouse in customers" :key="warehouse.id">{{warehouse.name}}</Option>
          </Select>


        </FormItem>


        <FormItem label="Түлхүүр үг">
          <Input type="text" v-model="filters.text" placeholder="Түлхүүр үг" />

        </FormItem>

        <Button type="ghost"  size="small" @click="resetFileter">Болих</Button>
        <Button type="primary" size="small" @click="doFilter">Хайх</Button>


      </Form>
    </Sider>


    <Modal
      v-model="table.showForm"
      title="Бараа"
    >



      <Form ref="table.form" :model="table.form" :rules="table.rule">


        <FormItem prop="transaction_type_id" label="Төрөл">


          <Select v-model="table.form.transaction_type_id" placeholder="Төрөл">
            <Option :value="`${warehouse.id}`" v-for="warehouse in transaction_types" :key="warehouse.id">{{warehouse.trans_type}}</Option>
          </Select>


        </FormItem>
        <FormItem prop="customer_id" label="Харилцагч">


          <Select v-model="table.form.customer_id" placeholder="Харилцагч">
            <Option :value="`${warehouse.id}`" v-for="warehouse in customers" :key="warehouse.id">{{warehouse.name}}</Option>
          </Select>


        </FormItem>

        <FormItem prop="warehouse_id" label="Агуулах">


          <Select v-model="table.form.warehouse_id" placeholder="Агуулах">
            <Option :value="`${warehouse.id}`" v-for="warehouse in warehouses" :key="warehouse.id">{{warehouse.warehouse}}</Option>
          </Select>


        </FormItem>
        <FormItem prop="product_id" label="Бараа">

          <Select v-model="table.form.product_id" placeholder="Бараа">
            <Option :value="`${type_.id}`" v-for="type_ in products" :key="type_.id">{{type_.product_name}}</Option>
          </Select>



        </FormItem>

        <FormItem prop="trans_number" label="Дугаар">
          <Input type="text" v-model="table.form.trans_number" placeholder="Дугаар" />

        </FormItem>


        <FormItem prop="title" label="Тайлбар">
          <Input type="textarea" v-model="table.form.trans_desc" placeholder="Тайлбар" />

        </FormItem>

        <FormItem prop="wr_trans_date" label="Огноо">
          <DatePicker  v-model="table.form.trans_date" format="yyyy/MM/dd" placeholder="Огноо" />

        </FormItem>

        <FormItem prop="total" label="Тоо">
          <Input type="text" v-model="table.form.total" placeholder="Тоо" />

        </FormItem>


        <FormItem prop="price" label="Үнэ">
          <Input type="text" v-model="table.form.price" placeholder="Үнэ" />

        </FormItem>


      </Form>

      <div slot="footer">
        <Button type="ghost" @click="resetForm('table.form')">Болих</Button>
        <Button type="primary" @click="handleSubmit('table.form')">Хадгалах</Button>
      </div>
    </Modal>

  </Layout>
</template>

<script>
  import moment from 'moment'
  import {transactionList, transactionAdd, transactionUpdate, transactionDelete ,forTransaction} from '../resoures/index'
  export default {
    methods:{
      getDate(d) {

        return moment(d, 'x').format("YYYY-MM-DD")
      },
      resetFileter(){
        this.filters = {
          products:undefined,
          transaction_type:undefined,
          customer:undefined,
          warehouse:undefined,
          text:undefined
        };
        this.doFilter();
      },
      doFilter(){
        this.table.options.product = this.filters.product;
        this.table.options.searchWord = this.filters.text;

        this.table.options.warehouse = this.filters.warehouse;
        this.table.options.customer =this.filters.customer;
        this.table.options.transaction_type =this.filters.transaction_type;

        this.getData(1);
      },
      changeSort(order){
        if(order.key == 'warehouse'){
          this.table.columns[1].sortType = order.order

        }
        if(order.key == 'wr_type_name'){
          this.table.columns[0].sortType = order.order
        }

        this.table.options.orderColumn = order.key;
        this.table.options.orderTo = order.order;

        this.getData(1);

        return false;
      },
      getData(page){
        transactionList(this.table.options, page ? page : 1).then(response=>{
          this.table.data = response.data
        }).catch(error=>{
          this.$Message.error('Алдаа');
        })
      },

      resetForm(name){
        if(name == 'table.form'){
          this.table.form = {
            warehouse_id: null,
            product_id: null,
            title: null,
            wr_trans_date: null,
            count: null,
          };
          this.table.editing = null;
          this.table.showForm = false;
        }
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            if(name == 'table.form'){
              if(this.table.editing){
                transactionUpdate({
                  id:this.table.editing,
                  warehouse_id:this.table.form.warehouse_id,
                  product_id:this.table.form.product_id,
                  customer_id:this.table.form.customer_id,
                  transaction_type_id:this.table.form.transaction_type_id,
                  trans_date:this.getDate(this.table.form.trans_date),
                  trans_number:this.table.form.trans_number,
                  trans_desc:this.table.form.trans_desc,
                  price:this.table.form.price,
                  total:this.table.form.total,


                }).then(response=>{
                  if(response.data == 'success'){
                    this.table.form = {
                      trans_number: null,
                      trans_desc: null,
                      price: null,
                      total: null,
                      trans_date: null,
                      warehouse_id: null,
                      product_id: null,
                      customer_id: null,
                      transaction_type_id: null,
                    };
                    this.table.editing = null;
                    this.$Message.success('Хадаглагдлаа!');
                    this.table.showForm = false;
                    this.getData(1);
                  }

                }).catch(error=>{
                  this.$Message.error('Алдаа!');
                })
              } else {
                transactionAdd(
                  {
                    warehouse_id:this.table.form.warehouse_id,
                    product_id:this.table.form.product_id,
                    customer_id:this.table.form.customer_id,
                    transaction_type_id:this.table.form.transaction_type_id,
                    trans_date:this.getDate(this.table.form.trans_date),
                    trans_number:this.table.form.trans_number,
                    trans_desc:this.table.form.trans_desc,
                    price:this.table.form.price,
                    total:this.table.form.total,
                  }).then(response=>{
                  if(response.data == 'success'){
                    this.table.form = {
                      trans_number: null,
                      trans_desc: null,
                      price: null,
                      total: null,
                      trans_date: null,
                      warehouse_id: null,
                      product_id: null,
                      customer_id: null,
                      transaction_type_id: null,
                    };
                    this.$Message.success('Хадаглагдлаа!');
                    this.table.showForm = false;
                    this.getData(1);
                  }

                }).catch(error=>{
                  this.$Message.error('Алдаа!');
                })
              }

            }

          } else {
            this.$Message.error('Мэдээллээ бүрэн оруулна уу!');
          }
        })
      },
      deteteType(id){

        transactionDelete({id:id}).then(response=>{
          this.getData(1);
        })
      },
      updateType(row){

        this.table.editing = row.id;
        this.table.showForm = true;
        this.table.form.warehouse_id = row.warehouse_id.toString();
        this.table.form.product_id = row.product_id.toString();
        this.table.form.customer_id = row.customer_id.toString();
        this.table.form.transaction_type_id = row.transaction_type_id.toString();
        this.table.form.trans_desc = row.trans_desc;
        this.table.form.trans_date = row.trans_date;
        this.table.form.trans_number = row.trans_number.toString();
        this.table.form.total = row.total.toString();
        this.table.form.price = row.price.toString();






      }
    },
    data() {
      return {


        table:{
          options:{
            product:undefined,
            warehouse:undefined,
            customer:undefined,
            transaction_type:undefined,
            orderColumn:undefined,
            orderTo:undefined,
            searchWord:undefined,
          },
          showForm:false,
          editing:null,
          form: {
            trans_number: null,
            trans_desc: null,
            price: null,
            total: null,
            trans_date: null,
            warehouse_id: null,
            product_id: null,
            customer_id: null,
            transaction_type_id: null,



          },
          rule: {
            customer_id: [
              { required: true, message: 'Харилцагч сонгоно уу', trigger: 'change' },
            ],
            transaction_type_id: [
              { required: true, message: 'Төрөл сонгоно уу', trigger: 'change' },
            ],

            warehouse_id: [
              { required: true, message: 'Агуулах сонгоно уу', trigger: 'change' },
            ],
            product_id: [
              { required: true, message: 'Бараа сонгоно уу', trigger: 'change' },
            ],

            trans_desc: [
              { required: true, message: 'Тайлбар оруулна уу', trigger: 'blur' },
            ],

            trans_number: [
              { required: true, message: 'Дугаар оруулна уу', trigger: 'blur' },
            ],
            trans_date: [
              { type:'date', required: true, message: ' Огноо оруулна уу', trigger: 'change' },
            ],
            total: [
              { required: true, message: ' Тоо оруулна уу', trigger: 'blur' },
            ],
            price: [
              { required: true, message: 'Үнэ оруулна уу', trigger: 'blur' },
            ],

          },
          columns: [
            {
              title: 'Төрөл',
              key: 'trans_type',
              sortable: true
            },
            {
              title: 'Агуулах',
              key: 'warehouse',
              sortable: true
            },

            {
              title: 'Бараа',
              key: 'product_name',
              sortable: true
            },

            {
              title: 'Харилцагч',
              key: 'customer_name',
              sortable: true
            },
            {
              title: 'Дугаар',
              key: 'trans_number',
              sortable: true
            },
            {
              title: 'Тайлбар',
              key: 'trans_desc',
              sortable: true
            },
            {
              title: 'Үнэ',
              key: 'price',
              sortable: true
            },
            {
              title: 'Тоо',
              key: 'total',
              sortable: true
            },
            {
              title: 'Огноо',
              key: 'trans_date',
              sortable: true
            },

            {
              title: 'Үйлдэл',
              key: 'id',
              width:100,
              // fixed:'right',
              render: (h, params) => {
                return h('div', [
                  h('Button', {
                    props: {
                      type: 'ghost',
                      size: 'small',
                      icon: 'ivu-icon ivu-icon-edit',
                    },
                    on: {
                      click: () => {

                        this.updateType(params.row)
                      }
                    }
                  }),
                  h('Poptip', {
                    props: {
                      confirm: true,
                      title: 'Устгахдаа итгэлтэй байна уу !',
                      okText:'Устгах',
                      cancelText:'Болих',

                      placement:"left-start"

                    },
                    on: {
                      "on-ok": () => {

                        this.deteteType(params.row.id)
                      }
                    }

                  }, [
                    h('Button', {
                      props: {
                        type: 'ghost',
                        size: 'small',
                        icon: 'ivu-icon ivu-icon-trash-a',
                      },

                    }),
                  ]),



                ]);
              }
            },

          ],
          data:{
            last_page:1,
            total:0,
            per_page:10,
            data:[]
          }
        },

        filters:{
          products:undefined,
          transaction_type:undefined,
          customer:undefined,
          warehouse:undefined,
          text:undefined
        },
        warehouses:[],
        products:[],
        customers:[],
        transaction_types:[],




      }
    },
    mounted(){
      this.getData(1);
      forTransaction().then(response=>{
        this.warehouses = response.data.warehouses;
        this.products = response.data.products;
        this.customers = response.data.customers;
        this.transaction_types = response.data.transaction_types;
      });

    }
  }
</script>



