import axios from 'axios';

const baseUrl = "http://eer.test/warehouse/";
axios.defaults.headers.common['X-CSRF-TOKEN'] = document.querySelector('meta[name="csrf-token"]').getAttribute('content');


/*
* warehouse type
* */
export function warehouseTypeList() {
  return axios.get(baseUrl + 'warehouseTypeList');
};

export function warehouseTypeAdd(data) {
  return axios.post(baseUrl + 'warehouseTypeAdd', data);
};

export function warehouseTypeUpdate(data) {
  return axios.post(baseUrl + 'warehouseTypeUpdate', data);
};

export function warehouseTypeDelete(data) {
  return axios.post(baseUrl + 'warehouseTypeDelete', data);
};
