<template>
  <div  class="page-content">
    <div class="page-datatable">



        <Table :columns="table.columns" :data="table.data.data" @on-sort-change="changeSort" size="small"></Table>

      <div class="pagination-wrapper">
        <Page :total="table.data.total" @on-change="getData" size="small" :page-size="table.data.per_page"></Page>
      </div>

    </div>
    <div class="page-aside">

      <div class="side-header">
        <Button type="success" icon="ivu-icon ivu-icon-plus" @click="table.showForm = true">Бараа нэмэх</Button>
        <Button type="ghost" icon="ivu-icon ivu-icon-gear-b" @click="$parent.$options.methods.showSettingsModal2($parent)">Тохиргоо</Button>
      </div>


      <div>
        <h2>
          Мэдээлэл шүүх
        </h2>





      </div>

      <Form >
        <FormItem prop="product_type_id" label="Төрөл">

          <Select v-model="filters.type" placeholder="Төрөл" clearable>
            <Option :value="`${type_.id}`" v-for="type_ in $parent.productType.data" :key="type_.id">{{type_.pro_type_name}}</Option>
          </Select>



        </FormItem>
        <FormItem prop="product_pack_id" label="Багц">


          <Select v-model="filters.pack" placeholder="Багц" clearable>
            <Option :value="`${type_.id}`" v-for="type_ in $parent.productPack.data" :key="type_.id">{{type_.pack_name}}</Option>
          </Select>



        </FormItem>



        <FormItem label="Түлхүүр үг">
          <Input type="text" v-model="filters.text" placeholder="Түлхүүр үг" />

        </FormItem>

        <Button type="primary" @click="doFilter">Шүүх</Button>
        <Button type="ghost"  @click="resetFileter">Цэвэрлэх</Button>


      </Form>
    </div>


    <Modal
      v-model="table.showForm"
      title="Бараа"
    >



      <Form ref="table.form" :model="table.form" :rules="table.rule">
        <FormItem prop="product_type_id" label="Төрөл">

            <Select v-model="table.form.product_type_id" placeholder="Төрөл">
              <Option :value="`${type_.id}`" v-for="type_ in $parent.productType.data" :key="type_.id">{{type_.pro_type_name}}</Option>
            </Select>



        </FormItem>
        <FormItem prop="product_pack_id" label="Багц">


            <Select v-model="table.form.product_pack_id" placeholder="Багц">
              <Option :value="`${type_.id}`" v-for="type_ in $parent.productPack.data" :key="type_.id">{{type_.pack_name}}</Option>
            </Select>



        </FormItem>



        <FormItem prop="product_name" label="Нэр">
          <Input type="text" v-model="table.form.product_name" placeholder="Нэр" />

        </FormItem>

        <FormItem prop="product_code" label="Код">
          <Input type="text" v-model="table.form.product_code" placeholder="Код" />

        </FormItem>

        <FormItem prop="price" label="Үнэ">
          <Input type="text" v-model="table.form.price" placeholder="Үнэ" />

        </FormItem>


      </Form>

      <div slot="footer">
        <Button type="ghost" @click="resetForm('table.form')">Болих</Button>
        <Button type="primary" @click="handleSubmit('table.form')">Хадгалах</Button>
      </div>
    </Modal>

  </div>
</template>

<script>
  import {productList, productAdd, productUpdate, productDelete ,forproduct} from '../../resoures/index'
  export default {
    methods:{
      resetFileter(){
        this.filters = {
          type:undefined,
          pack:undefined,
          warehouse:undefined,
          text:undefined
        };
        this.doFilter();
      },
      doFilter(){
        this.table.options.type = this.filters.type;
        this.table.options.searchWord = this.filters.text;
        this.table.options.pack = this.filters.pack;
        this.table.options.warehouse = this.filters.warehouse;

        this.getData(1);
      },
      changeSort(order){
        if(order.key == 'warehouse'){
          this.table.columns[1].sortType = order.order

        }
        if(order.key == 'wr_type_name'){
          this.table.columns[0].sortType = order.order
        }

        this.table.options.orderColumn = order.key;
        this.table.options.orderTo = order.order;

        this.getData(1);

        return false;
      },
      getData(page){
        productList(this.table.options, page ? page : 1).then(response=>{
          this.table.data = response.data
        }).catch(error=>{
          this.$Message.error('Алдаа');
        })
      },

      resetForm(name){
        if(name == 'table.form'){
          this.table.form = {

            product_type_id: null,
            product_pack_id: null,
            price: null,
            product_code: null,
            product_name: null,
          };
          this.table.editing = null;
          this.table.showForm = false;
        }
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            if(name == 'table.form'){
              if(this.table.editing){
                productUpdate({
                  id:this.table.editing,

                  product_type_id:this.table.form.product_type_id,
                  product_pack_id:this.table.form.product_pack_id,
                  price:this.table.form.price,
                  product_code:this.table.form.product_code,
                  product_name:this.table.form.product_name,

                }).then(response=>{
                  if(response.data == 'success'){
                    this.table.form = {

                      product_type_id: null,
                      product_pack_id: null,
                      price: null,
                      product_code: null,
                      product_name: null,
                    };
                    this.table.editing = null;
                    this.$Message.success('Хадаглагдлаа!');
                    this.table.showForm = false;
                    this.getData(1);
                  }

                }).catch(error=>{
                  this.$Message.error('Алдаа!');
                })
              } else {
                productAdd(
                  {

                    product_type_id:this.table.form.product_type_id,
                    product_pack_id:this.table.form.product_pack_id,
                    price:this.table.form.price,
                    product_code:this.table.form.product_code,
                    product_name:this.table.form.product_name,
                  }).then(response=>{
                  if(response.data == 'success'){
                    this.table.form = {

                      product_type_id: null,
                      product_pack_id: null,
                      price: null,
                      product_code: null,
                      product_name: null,
                    };
                    this.$Message.success('Хадаглагдлаа!');
                    this.table.showForm = false;
                    this.getData(1);
                  }

                }).catch(error=>{
                  this.$Message.error('Алдаа!');
                })
              }

            }

          } else {
            this.$Message.error('Мэдээллээ бүрэн оруулна уу!');
          }
        })
      },
      deteteType(id){

        productDelete({id:id}).then(response=>{
          this.getData(1);
        })
      },
      updateType(row){

        console.log(row)
        this.table.editing = row.id;
        this.table.showForm = true;

        this.table.form.product_type_id = row.product_type_id.toString();
        this.table.form.product_pack_id = row.product_pack_id.toString();
        this.table.form.price = row.price;
        this.table.form.product_code = row.product_code;
        this.table.form.product_name = row.product_name;

      }
    },
    data() {
      return {


        table:{
          options:{
            type:undefined,
            pack:undefined,
            warehouse:undefined,
            orderColumn:undefined,
            orderTo:undefined,
            searchWord:undefined,
          },
          showForm:false,
          editing:null,
          form: {

            product_type_id: null,
            product_pack_id: null,
            price: null,
            product_code: null,
            product_name: null,

          },
          rule: {

            product_type_id: [
              { required: true, message: 'Барааны төрөл сонгоно уу', trigger: 'change' },
            ],
            product_pack_id: [
              { required: true, message: 'Барааны багц сонгоно уу', trigger: 'change' },
            ],
            price: [
              { required: true, message: 'Үнэ оруулна уу', trigger: 'blur' },
            ],
            product_code: [
              { required: true, message: 'Код оруулна уу', trigger: 'blur' },
            ],
            product_name: [
              { required: true, message: 'Барааны нэр оруулна уу', trigger: 'blur' },
            ],

          },
          columns: [

            {
              title: 'Төрөл',
              key: 'pro_type_name',
              sortable: true
            },
            {
              title: 'Багц',
              key: 'pack_name',
              sortable: true
            },
            {
              title: 'Бараа',
              key: 'product_name',
              sortable: true
            },
            {
              title: 'Код',
              key: 'product_code',
              sortable: true
            },
            {
              title: 'Үнэ',
              key: 'price',
              sortable: true
            },
            {
              title: '...',
              align: 'center',
              key: 'id',
              width: 150,
              render: (h, params) => {
                return h('div', [
                  h('Button', {
                    props: {
                      type: 'primary',
                      size: 'small',

                    },
                    style: {
                      marginRight: '5px'
                    },
                    on: {
                      click: () => {

                        this.updateType(params.row)
                      }
                    }
                  }, 'Засах'),
                  h('Poptip', {
                    props: {
                      confirm: true,
                      title: 'Устгахдаа итгэлтэй байна уу !',
                      okText:'Устгах',
                      cancelText:'Болих',

                      placement:"left-start"

                    },
                    on: {
                      "on-ok": () => {

                        this.deteteType(params.row.id)
                      }
                    }

                  }, [
                    h('Button', {
                      props: {
                        type: 'error',
                        size: 'small'
                      },

                    }, 'Устгах'),
                  ]),



                ]);
              }
            },

          ],
          data:{
            last_page:1,
            total:0,
            per_page:10,
            data:[]
          }
        },

        filters:{
          type:undefined,
          text:undefined
        },
        warehouses:[]




      }
    },
    mounted(){
      this.getData(1);
      forproduct().then(response=>this.warehouses = response.data);

    }
  }
</script>



