<template>
  <div class="page-content">
    <div class="page-datatable">


      <Table :columns="table.columns" :data="table.data.data" @on-sort-change="changeSort" size="small"></Table>
      <div class="pagination-wrapper">
        <Page :total="table.data.total" @on-change="getData" size="small" :page-size="table.data.per_page"></Page>
      </div>
    </div>
    <div class="page-aside">

      <div class="side-header">
        <Button type="success" icon="ivu-icon ivu-icon-plus"  @click="table.showForm = true">Шилжилт хийх</Button>
        <Button type="ghost" icon="ivu-icon ivu-icon-gear-b"  @click="$parent.$options.methods.showSettingsModal($parent)">Тохиргоо</Button>
      </div>


      <div>
        <h2>
          Мэдээлэл шүүх
        </h2>




      </div>

      <Form >
        <FormItem prop="product" label="Бараа">

          <Select v-model="filters.product" placeholder="Бараа" clearable>
            <Option :value="`${type_.id}`" v-for="type_ in products" :key="type_.id">{{type_.product_name}}</Option>
          </Select>



        </FormItem>

        <FormItem prop="warehouse_id" label="Агуулах">


          <Select v-model="filters.warehouse" placeholder="Агуулах" clearable>
            <Option :value="`${warehouse.id}`" v-for="warehouse in warehouses" :key="warehouse.id">{{warehouse.warehouse}}</Option>
          </Select>


        </FormItem>


        <FormItem label="Түлхүүр үг">
          <Input type="text" v-model="filters.text" placeholder="Түлхүүр үг" />

        </FormItem>

        <Button type="primary" @click="doFilter">Шүүх</Button>
        <Button type="ghost"  @click="resetFileter">Цэвэрлэх</Button>


      </Form>
    </div>


    <Modal
      v-model="table.showForm"
      title="Агуулах хоорондын шилжилт"
    >



      <Form ref="table.form" :model="table.form" :rules="table.rule">


        <FormItem prop="warehouse_id" label="Агуулах">


          <Select v-model="table.form.warehouse_id" placeholder="Агуулах">
            <Option :value="`${warehouse.id}`" v-for="warehouse in warehouses" :key="warehouse.id">{{warehouse.warehouse}}</Option>
          </Select>


        </FormItem>
        <FormItem prop="product_id" label="Бараа">

          <Select v-model="table.form.product_id" placeholder="Бараа">
            <Option :value="`${type_.id}`" v-for="type_ in products" :key="type_.id">{{type_.product_name}}</Option>
          </Select>



        </FormItem>

        <FormItem prop="title" label="Тайлбар">
          <Input type="text" v-model="table.form.title" placeholder="Тайлбар" />

        </FormItem>

        <FormItem prop="wr_trans_date" label="Огноо">
          <DatePicker  v-model="table.form.wr_trans_date" format="yyyy/MM/dd" placeholder="Огноо" />

        </FormItem>

        <FormItem prop="count" label="Тоо">
          <Input type="text" v-model="table.form.count" placeholder="Тоо" />

        </FormItem>


      </Form>

      <div slot="footer">
        <Button type="ghost" @click="resetForm('table.form')">Болих</Button>
        <Button type="primary" @click="handleSubmit('table.form')">Хадгалах</Button>
      </div>
    </Modal>

  </div>
</template>

<script>
  import moment from 'moment'
  import {warehouseTranList, warehouseTranAdd, warehouseTranUpdate, warehouseTranDelete ,forwarehouseTrans} from '../../resoures/index'
  export default {
    methods:{
      getDate(d) {

        return moment(d, 'x').format("YYYY-MM-DD")
      },
      resetFileter(){
        this.filters = {
          product:undefined,
          warehouse:undefined,
          text:undefined
        };
        this.doFilter();
      },
      doFilter(){
        this.table.options.product = this.filters.product;
        this.table.options.searchWord = this.filters.text;

        this.table.options.warehouse = this.filters.warehouse;

        this.getData(1);
      },
      changeSort(order){
        if(order.key == 'warehouse'){
          this.table.columns[1].sortType = order.order

        }
        if(order.key == 'wr_type_name'){
          this.table.columns[0].sortType = order.order
        }

        this.table.options.orderColumn = order.key;
        this.table.options.orderTo = order.order;

        this.getData(1);

        return false;
      },
      getData(page){
        warehouseTranList(this.table.options, page ? page : 1).then(response=>{
          this.table.data = response.data
        }).catch(error=>{
          this.$Message.error('Алдаа');
        })
      },

      resetForm(name){
        if(name == 'table.form'){
          this.table.form = {
            warehouse_id: null,
            product_id: null,
            title: null,
            wr_trans_date: null,
            count: null,
          };
          this.table.editing = null;
          this.table.showForm = false;
        }
      },
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            if(name == 'table.form'){
              if(this.table.editing){
                warehouseTranUpdate({
                  id:this.table.editing,
                  warehouse_id:this.table.form.warehouse_id,
                  product_id:this.table.form.product_id,
                  title:this.table.form.title,
                  wr_trans_date:this.getDate(this.table.form.wr_trans_date),
                  count:this.table.form.count,


                }).then(response=>{
                  if(response.data == 'success'){
                    this.table.form = {
                      warehouse_id: null,
                      product_id: null,
                      title: null,
                      wr_trans_date: null,
                      count: null,
                    };
                    this.table.editing = null;
                    this.$Message.success('Хадаглагдлаа!');
                    this.table.showForm = false;
                    this.getData(1);
                  }

                }).catch(error=>{
                  this.$Message.error('Алдаа!');
                })
              } else {
                warehouseTranAdd(
                  {
                    warehouse_id:this.table.form.warehouse_id,
                    product_id:this.table.form.product_id,
                    title:this.table.form.title,
                    wr_trans_date:this.getDate(this.table.form.wr_trans_date),
                    count:this.table.form.count,
                  }).then(response=>{
                  if(response.data == 'success'){
                    this.table.form = {
                      warehouse_id: null,
                      product_id: null,
                      title: null,
                      wr_trans_date: null,
                      count: null,
                    };
                    this.$Message.success('Хадаглагдлаа!');
                    this.table.showForm = false;
                    this.getData(1);
                  }

                }).catch(error=>{
                  this.$Message.error('Алдаа!');
                })
              }

            }

          } else {
            this.$Message.error('Мэдээллээ бүрэн оруулна уу!');
          }
        })
      },
      deteteType(id){

        warehouseTranDelete({id:id}).then(response=>{
          this.getData(1);
        })
      },
      updateType(row){

        this.table.editing = row.id;
        this.table.showForm = true;
        this.table.form.warehouse_id = row.warehouse_id.toString();
        this.table.form.product_id = row.product_id.toString();
        this.table.form.title = row.title;
        this.table.form.wr_trans_date = row.wr_trans_date;
        this.table.form.count = row.count.toString();

      }
    },
    data() {
      return {


        table:{
          options:{
            product:undefined,

            warehouse:undefined,
            orderColumn:undefined,
            orderTo:undefined,
            searchWord:undefined,
          },
          showForm:false,
          editing:null,
          form: {
            warehouse_id: null,
            product_id: null,
            title: null,
            wr_trans_date: null,
            count: null,


          },
          rule: {
            warehouse_id: [
              { required: true, message: 'Агуулах сонгоно уу', trigger: 'change' },
            ],
            product_id: [
              { required: true, message: 'Бараа сонгоно уу', trigger: 'change' },
            ],

            title: [
              { required: true, message: 'Тайлбар оруулна уу', trigger: 'blur' },
            ],
            wr_trans_date: [
              { type:'date', required: true, message: ' Огноо оруулна уу', trigger: 'change' },
            ],
            count: [
              { required: true, message: ' Тоо оруулна уу', trigger: 'blur' },
            ],

          },
          columns: [
            {
              title: 'Агуулах',
              key: 'warehouse',
              sortable: true
            },

            {
              title: 'Бараа',
              key: 'product_name',
              sortable: true
            },
            {
              title: 'Тайлбар',
              key: 'title',
              sortable: true
            },
            {
              title: 'Огноо',
              key: 'wr_trans_date',
              sortable: true
            },
            {
              title: 'Тоо',
              key: 'count',
              sortable: true
            },
            {
              title: '...',
              align: 'center',
              key: 'id',
              width: 150,
              render: (h, params) => {
                return h('div', [
                  h('Button', {
                    props: {
                      type: 'primary',
                      size: 'small',

                    },
                    style: {
                      marginRight: '5px'
                    },
                    on: {
                      click: () => {

                        this.updateType(params.row)
                      }
                    }
                  }, 'Засах'),
                  h('Poptip', {
                    props: {
                      confirm: true,
                      title: 'Устгахдаа итгэлтэй байна уу !',
                      okText:'Устгах',
                      cancelText:'Болих',

                      placement:"left-start"

                    },
                    on: {
                      "on-ok": () => {

                        this.deteteType(params.row.id)
                      }
                    }

                  }, [
                    h('Button', {
                      props: {
                        type: 'error',
                        size: 'small'
                      },

                    }, 'Устгах'),
                  ]),



                ]);
              }
            },

          ],
          data:{
            last_page:1,
            total:0,
            per_page:10,
            data:[]
          }
        },

        filters:{
          type:undefined,
          text:undefined
        },
        warehouses:[],
        products:[]




      }
    },
    mounted(){
      this.getData(1);
      forwarehouseTrans().then(response=>{
        this.warehouses = response.data.warehouses;
        this.products = response.data.products;
      });

    }
  }
</script>



